import { Block } from '@ali/4ever-cangjie';

export default function getPositionOfRow(rowKey, table: Block) {
  const rowSize = table.nodes.length;
  for (let rowIndex = 0; rowIndex < rowSize; rowIndex += 1) {
    if (table.nodes[rowIndex].key === rowKey) {
      return rowIndex;
    }
  }
  return null;
}